import React from "react";
import Event from "./Event";
import {Card, Typography} from "antd";

const {Title} = Typography;
const {Meta} = Card;

export default function LatestEvent({events}) {
    return (
        <div className='event' style={{width: "100%"}}>
            <Title level={2}>Our Latest Events</Title>
            <div
                className='container'
                style={{display: "flex", gap: ".5rem", width: "100%"}}
            >
                {[1, 2, 3].map((item) => (
                    <Card cover={<img src='logo_alpha.png' />}>
                        <Meta
                            title='Europe Street beat'
                            description='www.instagram.com'
                        />
                    </Card>
                ))}
            </div>
        </div>
    );
}
